import { AdminService } from './../../../services/backend/admin.service';
import { Component, OnInit,OnDestroy } from '@angular/core';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import {Router} from '@angular/router';
import { Subject } from 'rxjs';
import * as $ from 'jquery';
@Component({
  selector: 'user-list',
  templateUrl: 'list.component.html'
})
export class ListComponent implements OnDestroy, OnInit {

 dtOptions: DataTables.Settings = {};
 //users:any;
  users: any;
  dtTrigger: Subject<any> = new Subject();
  public isChecked: boolean = true;  
  constructor(private http: HttpClient, private service: AdminService,private router: Router) {}

  	ngOnInit(): void {
		this.dtOptions = {
			pagingType: 'full_numbers',
			pageLength: 3,
			columnDefs: [ { orderable: false, targets: [4,5,6] } ]
		};  
		// Call gertuser service function //
			var user = 'user';
			this.service.getUsers(user).subscribe(res => {
			this.users = res;
			this.dtTrigger.next();
			//console.log(this.users);
			
		},err=>{		
			console.log(err);
			//this.router.navigate(['login']);    
		});
  	}
  
  //Enable-Disable User
  	enableDisable = (user_id,evt) => {    
		this.isChecked = evt.target.checked;
		var status = 2;
		if(this.isChecked){
			status = 1;
		}
		this.service.updateUser(user_id,status).subscribe(res => {
			// Change status text 
			if(res.status==1){
				$('.status_txt_'+user_id).html('Active');
				$('.status_txt_'+user_id).removeClass('badge-secondary');
				$('.status_txt_'+user_id).addClass('badge-success');
			}
			if(res.status==2){
				$('.status_txt_'+user_id).html('Inactive');
				$('.status_txt_'+user_id).removeClass('badge-success');
				$('.status_txt_'+user_id).addClass('badge-secondary');
			}
			},err=>{		
			console.log(err);
				
		});
		//alert(this.isChecked+user_id);
  	}
  
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  } 
}