import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from './../../../services/user.service';
import { JobService } from './../../../services/job.service';
import { environment } from '../../../../environments/environment';
import { AdminService } from './../../../services/backend/admin.service';

import { FormGroup,  FormBuilder,  Validators,FormArray,FormControl } from '@angular/forms';
// import { FormGroup,  FormBuilder,  Validators, FormControl } from '@angular/forms';
import { Subject } from 'rxjs';

@Component({
	templateUrl: 'edit.component.html'
})

export class EditComponent implements OnInit {
	angForm: FormGroup;
	submitted = false;
	dtOptions: DataTables.Settings = {};
	services: any;
	results:any;
	states:any = '';
	counties:any = '';
	stateName = '';
	statename = '';countyName = '';currentState='';currentCounty='';userid='';
	cities:any = '';
	data:any;
	serviceId: any;
	firstname: any;
	lastname: any;
	city: any;
	email: any;
	address: any;
	zipcode: any;
	phone: any;
	state:any;
	county:any;

	dtTrigger: Subject<any> = new Subject();
	constructor(private service: AdminService, private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private userservice: UserService, private jobservice: JobService) {
		this.LoginForm();
	}
	LoginForm() {
		this.angForm = this.fb.group({
			fname: ['', Validators.required],
			lName: ['', Validators.required],
			emailad: ['', Validators.required],
			StreetAdress: ['', Validators.required],
			zipcode: ['', Validators.required],
			state: ['', Validators.required],
			county: ['', Validators.required],
			city: ['', Validators.required],
			Phone: ['', Validators.compose([Validators.required,Validators.pattern('^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$')])]
			
		});
    }
	
	getCounties(event){
		this.stateName = event.target.value;
		this.userservice.getCounties(this.stateName).subscribe(result => {
			this.counties = result;
		});
		
	}  

	getCities(event){
		this.countyName = event.target.value;
		this.statename = this.angForm.value.state;
		 this.userservice.getCities(this.countyName,this.statename).subscribe(result => {
			this.cities = result;
		}); 
		
		
	}  

	/* On Submit service Update function is called*/
	
    updatedata() {
		this.submitted = true;
		
		if(this.angForm.invalid) {
			return;
        } else {
			this.serviceId = this.route.snapshot.paramMap.get('serviceId');
			if(this.angForm.value) {
				const obj = {'firstname': this.angForm.value.fname, 'phone': this.angForm.value.Phone, 'address': this.angForm.value.StreetAdress, 
				'zipcode': this.angForm.value.zipcode, 'lastname': this.angForm.value.lName, 'email':this.angForm.value.emailad, 
				'county':this.angForm.value.county,'state':this.angForm.value.state,'city':this.angForm.value.city};
				this.service.updateusersbyid(this.serviceId, obj).subscribe(res => {
					this.data = res.message;
					setTimeout(() => {
						this.router.navigate(['/admin/users/list'])
					}, 2000);
				}, err => {
					console.log(err); //   this.router.navigate(['services']);
				});
			} else {
				window.alert('Please add the service name');
			}
		}	
        
    }

	get f() { return this.angForm.controls; }

	ngOnInit(): void {

		this.dtOptions = {
			pagingType: 'full_numbers',
			pageLength: 3,
			columnDefs: [{ orderable: false, targets: [3] }]
		};
		this.serviceId = this.route.snapshot.paramMap.get('serviceId');
		this.service.userseditservicecall(this.serviceId).subscribe(res => {
			console.log(res.data);
			this.results = res.data;
			var currentState = this.results.state;
			var currentCounty = this.results.county;
			var user_pic = this.results.profile_pic;
			var user_id = this.results._id;
			
			this.userservice.getStates().subscribe(result => {
				this.states = result;
			});

			if(currentState != undefined || currentState != 'undefined') {
				this.userservice.getCounties(currentState).subscribe(result => {
					this.counties = result;
				}); 
			} else {
				this.userservice.getCounties(this.states).subscribe(result => {
					this.counties = result;
				}); 
			}
			
			if(currentCounty != undefined || currentCounty != 'undefined') {
				this.userservice.getCities(currentCounty,currentState).subscribe(result => {
					this.cities = result; 
				});
			} else {
				this.userservice.getCities(this.counties, this.states).subscribe(result => {
					this.counties = result;
				}); 
			}

			this.firstname = this.results.firstname;
			this.lastname = this.results.lastname;
			this.city = this.results.city;
			this.state = this.results.state;
			this.county = this.results.county;
			this.email = this.results.email;
			this.address =  this.results.address;
			this.zipcode = this.results.zipcode;
			this.phone =  this.results.phone;
		


			this.dtTrigger.next();
		}, err => {
			console.log(err); //   this.router.navigate(['login']);
		});
	}
}